.booksDropdown{
    display: inline-block;
    color: black;
    font-size: 75%;
    width: 15vw;
    margin-left: 10%;
}

.chapterDropdown{
    display: inline-block;
    color: black;
    font-size: 75%;
    width: 8vw;
    margin-left: 10%;
}

.dropDownSameLine{
    display: inline-block;
    margin-left: 5%;
    z-index: 10;
    font-family:serif;
}
.dropDownSameLine1{
    display: inline-block;
    margin-left: 5%;
    z-index: 10;
    font-family:serif;
}
.dropDownSameLine2{
    display: inline-block;
    margin-left: 5%;
    z-index: 10;
    font-family:serif;
}

.allDropDowns{
    display: flex;
}

.submit{
    margin-left: 5%;
    margin-top: 1%;
    z-index: 10;
}

.flex{
    display:inline-table;
}

.full{
    height:100%;
    width: 100%;
}

.book{
    width:100%;
}

.correct{
    color: rgb(1, 151, 1);
}

.red{
    width: 100%;
}

.chapter{
    margin-left:40%;
    width:100%;
    position: relative;
}

.newTestament{
    font-size: 50%;
    margin-left: 3%;
    z-index: 100;
    margin-top: 1%;
}

.refresh{
    margin-left: 1%;
    margin-top: 1%;
    z-index: 10;
}

.guess{
    position:relative;
    width:100%;
    float:right;
    height:45vh;
    top: -20vh;
}

.guessDiv{
    position: fixed;
    top: 45vh;
    left: 29%;
}

.bookDiv{
    position: fixed;
    top: 45vh;
    left: 35%;
    font-family:serif;
}

.fullLine{
    width: 3000%;
    background-color: red;
    margin-top: 70%;
}

.nonGuesses{
    margin-top: -5%;
}

.realVerse{
    color: black;
    font-family: serif;
}

.close{
    color: black;
    background-color: yellow;
}

.chapterDiv{
    float: left;
    position: fixed;
    top: 45vh;
    left: 45%;
    font-family:serif;
}

.verseDiv{
    position: fixed;
    top: 45vh;
    left: 42%;
    font-family:serif;
}
.verse {
    margin-left:300%;
    width:100%;
}

.verseText{
    color: rgb(0, 0, 0);
    font-family:serif;
    width: 70%;
    margin-left: 15%;
}

.curStreak{
    position: absolute;
    margin-top: 2%;
    font-family:serif;
}

.right{
    position: absolute;
    margin-top: 6%;
    font-family:serif;
    background-color: green;
}

.wrong{
    position: absolute;
    margin-top: 10%;
    font-family:serif;
    background-color: rgb(193, 0, 0);
}
