@media only screen and (max-width: 450px) {

    .chapterDiv{
        float: left;
        position: fixed;
        top: 55vh;
        left: 30%;
        font-size: 75%;
    }

    .bookDiv{
        position: fixed;
        top: 55vh;
        left: 8%;
        font-size: 75%;
    }

    .verseDiv{
        position: fixed;
        top: 55vh;
        left: 22%;
        height: 125%;
        font-size: 75%;
    }

}

@media only screen and (max-width: 780px) {
    .chapterDropdown{
        display: inline-block;
        color: black;
        font-size: 75%;
        width: 20vw;
        margin-left: 10%;
        z-index: 11;
        font-size: 50%;
    }

    .booksDropdown{
        display: inline-block;
        color: black;
        font-size: 50%;
        width: 30vw;
        margin-left: -8%;
    }

    .submit{
        margin-top: -2%;
        height: 3%;
        z-index: 10;
        position: absolute;
        left: 40%;
    }

    .fullLine{
        width: 3000%;
        background-color: red;
        margin-top: 75%;
    }

    .chapterDiv{
        float: left;
        position: fixed;
        top: 55vh;
        left: 30%;
    }

    .bookDiv{
        position: fixed;
        top: 55vh;
        left: 8%;
    }

    .verseDiv{
        position: fixed;
        top: 55vh;
        left: 22%;
        height: 125%;
    }

    .guessDiv{
        position: fixed;
        top: 55vh;
        left: 1%;
    }
    .newTestament{
        font-size: 50%;
        z-index: 10;
        top: 0vh;
        left: 18vh;
        position: fixed;
    }
    
    .refresh{
        top: 0vh;
        left: 30vh;
        z-index: 10;
        position: fixed;
    }

    .nonGuesses{
        margin-top: -10%;
    }

    .dropDownSameLine{
        display: inline-block;
        margin-left: 5%;
        margin-top: 5%;
        z-index: 10;
        font-family:serif;
    }
    .dropDownSameLine1{
        display: inline-block;
        margin-left: 5%;
        margin-top: 5%;
        z-index: 10;
        font-family:serif;
    }
    .dropDownSameLine2{
        display: inline-block;
        margin-left: 5%;
        margin-top: 5%;
        z-index: 10;
        font-family:serif;
    }
    .curStreak{
        position: absolute;
        margin-top: 5%;
        margin-left: 5%;
        font-family:serif;
    }
    
    .right{
        position: absolute;
        margin-left: 45%;
        margin-top: 5%;
        font-family:serif;
        background-color: green;
    }
    
    .wrong{
        position: absolute;
        margin-left: 75%;
        margin-top: 5%;
        font-family:serif;
        background-color: rgb(193, 0, 0);
    }
}